.exhibitions {
  @include pageGrid;
  @include paddingInline;
  margin-top: 90px;
  @include onTablet {
    margin-top: 105px;
  }

  &__title {
    @extend %h3;
    font-size: 28px;
    grid-column: span 2;
    text-align: center;
    @include onTablet {
      grid-column: 1 / 5;
      grid-row: 1;
      text-align: left;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    @include onDesktop {
      grid-column: 1 / 7;
    }
  }

  &__type {
    grid-column: span 2;
    height: fit-content;
    margin-top: 60px;
    @include onTablet {
      grid-column: span 3;
      margin-top: 75px;
    }
    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__link {
    display: block;
    width: 100%;
    margin: 0;
    @include onDesktop {
      height: 80%;
      position: relative;
      padding-bottom: 87%;
      margin-bottom: 20px;
    }
  }

  &__photo {
    width: 100%;
    height: 100%;
    margin-bottom: 20px;
    @include onTablet {
      margin-bottom: 30px;
    }
    @include onDesktop {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      object-fit: cover;
    }
    @include hover (transform,scale(1.05));
  }

  &__subtitle {
    @extend %h4;
    margin-top: 10px;
    margin-bottom: 20px;
    @include onTablet {
      margin-bottom: 50px;
      height: 60px;
    }
  }

  & .main-text {
    color: $c-gray;
    @include onTablet {
      height: 70px;
    }
    &--red {
      color: $c-main;
    }
  }

  & .button {
    margin-top: 30px;
    @include onTablet {
      margin-top: 40px;
      width: 100%;
    }
  }

  & .button--white {
    grid-column: span 2;
    margin-top: 30px;
    display: flex;
    justify-content: center;
    gap: 8px;

    @include onTablet {
      grid-column: 5 / -1;
      grid-row: 1;
      margin-top: 0;
    }
    @include onDesktop {
      grid-column: -4 / -1;
    }
  }
}
